@import url(https://fonts.googleapis.com/css2?family=Source+Serif+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.Dog{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    color: white;
    margin: 2rem 0;
    box-sizing: border-box;
    padding: 2rem;
    min-width: 30%;
    width: auto;
    border-radius: 1em;
    box-shadow: 2px 5px 7px rgba(0, 0, 0, .5);
    background: #c31432;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.Dog img{
    /* border-radius: 50%; */
    /* max-height: 250px; */
    max-width: 400px;
    margin: 2rem;
    box-shadow: 3px 10px 20px rgba(0, 0, 0, .5);
}

.Dog-facts{
    text-align: left;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border-bottom: 2px solid rgba(255, 255, 255, .2);
    min-width: 30%;
    padding: 2rem 1rem 1rem 2rem;;
    margin: 1rem;
    background-color: rgba(0, 0, 0, .4);
    color: white;
    border-radius: 10px;
}

.image-container{
    display: flex;
    justify-content: center;
    height: 100%;
    width: 350px;
}
.DogList img {
    width: 300px;
    transition: .5s ease;
}

.DogList img:hover{
    -webkit-filter: grayscale(5);
            filter: grayscale(5);
    transform: translateY(-4px);
    transition: .5s ease;
    box-shadow: 2px 5px 20px black;

}


hr.underline {
    overflow: visible; /* For IE */
    border-style: solid;
    border-color: white;
    width: 0;
    border-radius: 20px;
}


.DogList{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
}

.dog{
    margin-bottom: 2rem;
}

 .dog:hover .underline{
    animation: outline .5s ease-in-out forwards;
}
@keyframes outline{
100% {
   width:80%;
   background: white; 
}
}

